    content: '';
}

/* remember to define focus styles! */
:focus {
    outline: 0;
}

/* tables still need 'cellspacing="0"' in the markup */
table {
    border-collapse: collapse;
    border-spacing: 0;
}

*,
*:before,
*:after {
    box-sizing: border-box;
}

a:active,
a:hover {
    outline: 0;
}

footer,
header,
menu,
nav,
section {
    display: block;
}

