.mp__overlay {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    z-index: 100;
    background-color: rgba(0, 0, 0, 0.5);
    display: none;
}

.mp__panel {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    z-index: 101;
    padding: 0 5px 0 5px;
    text-align: center;
    background: var(--blue-light);
    display: none;
}

.mp__panel:after {
    content: '';
    display: table;
    clear: both;
}

.mp__button-main {
    width: 34px;
    height: 34px;
    margin: 3px;
    background: none;
    border: none;
    padding: 4px;
    border-radius: 3px;
    cursor: pointer;
    outline: none;
    float: right;
}

.mp__line {
    position: relative;
    display: block;
}

.mp__line:after,
.mp__line:before {
    content: "";
    position: absolute;
    left: 0;
    top: -9px;
}

.mp__line:after {
    top: 9px;
}

.mp__line,
.mp__line:after,
.mp__line:before {
    width: 100%;
    height: 2px;
    -webkit-transition: all 0.3s;
    transition: all 0.3s;
    -webkit-backface-visibility: hidden;
    backface-visibility: hidden;
    border-radius: 2px;
    background-color: currentColor;
}

.mp__button-main.mp--on .mp__line {
    background-color: transparent;
}

.mp__button-main.mp--on .mp__line:before {
    -webkit-transform: rotate(45deg) translate(6px, 7px);
    transform: rotate(45deg) translate(6px, 7px);
}

.mp__button-main.mp--on .mp__line:after {
    -webkit-transform: rotate(-45deg) translate(6px, -7px);
    transform: rotate(-45deg) translate(6px, -7px);
}



.mp__button-text {
    margin: 6px 6px 3px 3px;
    background: none;
    border: solid 1px #fff;
    padding: 6px 4px;
    border-radius: 3px;
    cursor: pointer;
    outline: none;
    font-size: 14px;
    float: left;
    color: currentColor;
}

.mp__button-text--center {
    float: none;

}

.mp--hide {
    display: none;
}

.mp__button {
    position: relative;
}

.mp__notification {
    position: absolute;
    top: -5px;
    right: -11px;
    background-color: #dd0000;
    color: #fff;
    padding: 1px 3px;
    border-radius: 10px;
    min-width: 12px;
}



@media screen and (min-width: 576px){

    
}

@media only screen and (max-width : 767px) {

    .mp__overlay.mp--on {
        display: block;
    }

    .mp__panel {
        display: block;
    }

    body.mp--init {
        padding-top: 40px;
    }


    .mp__nav-panel {
        display: block;
        position: fixed;
        top: 0;
        bottom: 0;
        width: 80%;
        max-width: 260px;
        overflow-y: auto;
        box-shadow: 0 0 3px 0 rgba(0, 0, 0, 0.8);
        z-index: 102;
        -webkit-box-sizing: border-box;
        box-sizing: border-box;
        background-color: var(--blue-light);
        margin: 0;
        padding: 45px 20px 20px 20px !important;
        height: auto !important;
    }

    .mp__nav-panel_main {
        left: -100%;
        transition: left 0.3s linear;
    }


    .mp__nav-panel_second {
        right: -125%;
        transition: right 0.3s linear;
    }

    .mp__nav-panel_main.mp--on {
        left: 0;
    }


    .mp__nav-panel_second.mp--on {
        right: 0;
    }

}


