    margin: 1.5em 0;
    font-size: 0.8em;
    line-height: 1.5em;
    background-color: #f5f5f5;
}

.u-content blockquote {
    padding: 1em 2em 0.5em;
    margin: 1.5em 0;
    font-size: 0.8em;
    line-height: 1.5em;
    background-color: #f5f5f5;
    quotes: """""""";
