.u-content blockquote:before {
    color: #666;
    content: open-quote;
    font-size: 4em;
    line-height: 0.1em;
    margin-right: 0.25em;
    vertical-align: -0.4em;
}

.u-content blockquote:after {
    color: #666;
    content: close-quote;
    font-size: 4em;
    line-height: 0.1em;
    margin-left: 0.25em;
    vertical-align: -0.6em;
}

.u-content pre {
    padding: 1em;
